footer {
    background : var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    color: var(--color-bg);
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;

}

.footer__socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a {
    color: var(--color-white);
    background: var(--color-bg);
    padding:0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
.footer__socials a:hover {
    background: transperent;
    color: var(--color-bg);
    border-color: var(--color-bg);

}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

/*========= MEDIA QUERIES (MEDIUM DEVICES) ==================*/
@media screen and (max-width: 1024px){
    .footer__socials{
        gap: 0.5rem;
    }
    
    .footer__socials a{
        padding: 0.5rem;
    }

    .footer__socials a:hover{
        padding: 0.5rem;
    }

    .footer__copyright {
        margin-bottom: 4rem;
        color: var(--color-bg)
    }
}/*========= MEDIA QUERIES (SMALL DEVICES) ==================*/
@media screen and (max-width: 600px){
    .permalinks{
        flex-direction: column;
        gap: 1.5rem;
    }
    .footer__socials{
        margin-bottom: 206rem;
    }

}